$font-stack: Helvetica, sans-serif;
$primary-color: #333;

// TODO: Phil, info title, peep this shit
.salient-customizer {
  &__info--title {
    font-size: 13px;
    line-height: 1.5;
  }

  &__info--text {
    font-size: 13px;
    line-height: 1.5;
  }
}


#customize-theme-controls .accordion-section-title {
  background-color: transparent;
  border: none;
  padding-left: 25px;
  font-weight: 400;
    &:after {
      top: calc(50% - 7px);
      font-size: 14px;
    }
}

.salient-customizer-title {
  height: auto;
  display: block !important;
  visibility: visible !important;
  padding: 30px 25px 10px 25px;
  border-bottom: none;


  h3 {
    font-size: 16px!important;
    font-weight: 600;
    text-transform: capitalize;
    color: #000;
    margin: 0;
  }
}

.salient-customizer-divider {
  height: auto;
  display: block !important;
  visibility: visible !important;
  border-bottom: none;
  padding: 10px 26px;

  &__line {
    margin-left: -25px;
    width: calc(100% + 50px);
    height: 1px;
    background-color:#C4C4C4;
  }
}
